.Card {
    background-color: #47a8bd;
    font-weight: bold; 
    /* background-color: #639A88; */
    display: flex;
    flex-flow: row;
    justify-content: center;
    width: 100%;
    height: fit-content;
    min-width: 300px;
    max-height: 100%;
    padding: 20px;
    border-bottom: 2px solid hsla(0, 0%, 0%, 0.527);
}

.Card h2 {
    margin: 0px auto;
    padding: 3px;
    width: fit-content;
    height: fit-content;
    border: 2px solid black;
}
.Card h3, p {
    margin: 10px auto;
    width: fit-content;
}
.Card text {
    margin: 10px auto;
    padding: 3px;
    width: fit-content;
    height: fit-content;
}
@media (max-width: 250px) {
    .Card {
        font-size: 14px;
    }
}